import React, {Component} from "react";
import PropTypes from "prop-types";
import _ from 'lodash';

export default class SummaryTableItem extends Component {

    componentDidMount() {
        if (this.props.tooltip) {
            OPL.UI.loadModules(document.getElementById(this.props.id), [{
                path: "core/modules/tooltips",
                options: {"mouseoverMinWidth": 0, "triggerEvent": "mouseover"},
            }]);
        }
    }

    render() {
        const textClass = "u-font-bold g-brand2-c u-no-margin";
        const hideElementOnMobile = " td-null";

        let monthlyPrice = (this.props.monthlyPrice || (this.props.monthlyPrice === 0 && !!this.props.monthFrom)) ?
            <span className={textClass}>{this.props.monthlyPrice.toFixed(2).replace(".", ",") + " " + this.props.currency}</span> : this.renderNoPrice("u-font-bold g-brand2-c");

        // for 'oplata na start' show oneTimePriceWithDeposit
        let oneTimePriceCost = (this.props.oneTimePrice && this.props.monthFrom === undefined && this.props.deposit != null)
            ? this.getOneTimePriceWithDeposit()
            : this.props.oneTimePrice;

        let oneTimePrice = (this.props.oneTimePrice) ?
            <span className={textClass}>{oneTimePriceCost}</span> : this.renderNoPrice("u-font-bold g-brand2-c");

        return (
            <tr id={this.props.id + "-summaryItem"} className="u-small-padding-l u-small-no-margin-top u-border-bottom">
                <th id={this.props.id} className="u-w-auto u-small-padding-top-l u-no-padding-left">
                    <span className="u-margin-right u-inline u-font-normal u-small-font-bold">{this.props.header}</span>
                    {(this.props.tooltip) && <a href="#" data-type="no-click" data-tooltip-trigger-event="mouseover" className="o-tooltip__trigger o-tooltip--top u-inline-block">
                        <span className="g-icon g-icon--hint g-icon--font g-icon--xs" />
                    </a>}
                    {(this.props.tooltip) && <p className="o-tooltip__content"> {this.props.tooltip}</p>}
                </th>
                <td data-title={this.props.oneTimeHeader}
                    className={"second-last u-font-normal u-small-padding" + (this.props.oneTimePrice ? "" : hideElementOnMobile)}>{oneTimePrice}</td>
                <td data-title={this.props.monthlyHeader}
                    className={"last u-font-normal  u-small-no-padding" + (this.props.monthlyPrice ? "" : hideElementOnMobile)}>{monthlyPrice}</td>
            </tr>
        )
    }

    getOneTimePriceWithDeposit() {
        if(!this.props.deposit || !this.props.oneTimePrice){
            return this.props.oneTimePrice;
        }
        let oneTimePriceWithDotWithCurrency = this.props.oneTimePrice.toString().replace(",", ".");
        let oneTimePriceWithDot = oneTimePriceWithDotWithCurrency.replace("zł", "");
        let depositWithDot = this.props.deposit.toString().replace(",", ".");

        let oneTimePriceWithDotNumber = parseFloat(oneTimePriceWithDot);
        let depositWithDotNumber = parseFloat(depositWithDot);

        let result = (oneTimePriceWithDotNumber+depositWithDotNumber).toString().replace(".", ",");

        if(result.split(",").length === 1){
            return result + ",00 zł";
        } else {
            return result + " zł";
        }
    }
    renderNoPrice(className=undefined){
        return (
            <div className='u-inline'>
                <span className={className} aria-hidden="true">&mdash;</span>
                <span className="u-acc-hide">brak</span>
            </div>
        )
    }
}

SummaryTableItem.propTypes = {
    header: PropTypes.string.isRequired,
    monthlyHeader: PropTypes.string.isRequired,
    oneTimeHeader: PropTypes.string.isRequired,
    monthlyPrice: PropTypes.number,
    oneTimePrice: PropTypes.string,
    highlighted: PropTypes.bool,
    tooltip: PropTypes.string,
    id: PropTypes.string,
};

SummaryTableItem.defaultProps = {
    highlighted: false,
};
